import React from 'react';
import axios from 'axios';
import InfiniteScroll from 'react-infinite-scroll-component';
import ReactDOM from 'react-dom';
import './index.css'; 

let photoId = 1
let delay = 1000 * 10; // 10 seconds each photo
setInterval(function() {
    window.location.hash = "#" + photoId;
    photoId++;
}, delay);

const UnsplashImage = ({ url, key, id }) => (
  <div className="image-item" key={key} id={id}>
    <img src={url} alt="ninth!"/>
  </div>
);

let Collage = () => {
  const [images, setImages] = React.useState([]);
  const [loaded, setIsLoaded] = React.useState(false);

  React.useEffect(() => {
    fetchImages();
  }, []);

  const fetchImages = (count = 10) => {
    const apiRoot = "https://api.unsplash.com";
    const accessKey =
      "c24234d52adedfd491a272d8400b6588a6eb8137229bd85e7e61ae8fdc0e8455";

    axios
      .get(`${apiRoot}/photos/random?client_id=${accessKey}&count=${count}`)
      .then(res => {
        setImages([...images, ...res.data]);
        setIsLoaded(true);
      });
  };

  return (
    <div className="hero is-fullheight is-bold is-info">
      <div className="hero-body">
        <div className="container">

          <InfiniteScroll
            dataLength={images}
            next={() => fetchImages(5)}
            hasMore={true}
            loader={
              <img
                src="https://9th-studio.github.io/assets/img/logo.png"
                alt="loading"
              />
            }
          >
            <div className="image-grid">
              {loaded
                ? images.map((image, index) => (
                    <UnsplashImage
                      url={image.urls.regular}
                      key={index}
                      id={index}
                    />
                  ))
                : ""}
            </div>
          </InfiniteScroll>
        </div>
      </div>
    </div>
  );
};

ReactDOM.render(<Collage />, document.getElementById("root"));
